import {
  HTMLAttributes,
  ImgHTMLAttributes,
  forwardRef,
  useCallback,
  useEffect,
  useRef,
} from 'react'

export interface ComicImageProps
  extends Omit<ImgHTMLAttributes<HTMLImageElement>, 'src' | 'srcSet'> {
  img: typeof import('*.png').default
  alt?: string
}

export const ComicImage = forwardRef<HTMLImageElement, ComicImageProps>(
  function ComicImage({ img, alt = '', ...props }, ref) {
    return (
      <img
        {...props}
        ref={ref}
        src={img.url['2x']}
        srcSet={img.srcSet}
        width={img.width}
        height={img.height}
        alt={alt}
        draggable="false"
      />
    )
  },
)

export const ComicImageAnimation = function ComicImageAnimation({
  imgs,
  rateMS = 0,
  showIdx = null,
  ...props
}: {
  imgs: Array<typeof import('*.png').default>
  rateMS?: number
  showIdx?: number | null
  animate?: boolean
} & HTMLAttributes<HTMLDivElement>) {
  const ref = useRef<HTMLImageElement>(null)
  const lastIdx = useRef<number | null>(null)

  const updateIdx = useCallback((idx: number) => {
    const { current: el } = ref
    if (!el) {
      return
    }
    if (lastIdx.current != null) {
      el.children[lastIdx.current].setAttribute('style', 'display: none')
    }
    el.children[idx].setAttribute('style', 'display: block')
    lastIdx.current = idx
  }, [])

  useEffect(() => {
    if (showIdx != null) {
      updateIdx(showIdx)
      return
    } else if (lastIdx.current == null) {
      // On first render, always show first frame, even if paused.
      updateIdx(0)
    }

    if (rateMS === 0) {
      return
    }

    let idx = 0
    const interval = setInterval(() => {
      idx = (idx + 1) % imgs.length
      updateIdx(idx)
    }, 1000 / rateMS)

    updateIdx(0)

    return () => {
      clearInterval(interval)
    }
  }, [imgs, rateMS, updateIdx, showIdx])

  return (
    <div
      ref={ref}
      css={{
        position: 'relative',
        width: imgs[0].width,
        height: imgs[0].height,
        img: {
          position: 'absolute',
          display: 'none',
        },
      }}
      {...props}
    >
      {imgs.map((img, idx) => (
        <ComicImage key={idx} img={img} />
      ))}
    </div>
  )
}
